import { useState, useCallback, useEffect, useRef } from 'react';
import { usePrevious, useWindowSize } from 'react-use';

import { useAlert } from "react-alert";

import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';

import './App.css';
import Row from './Row';
import { wordList } from './wordList';
import { dirtyWords } from './dirtyWords';
import { initialGuesses, keyboardLayout, keyboardDisplay, initialButtonTheme, keyboardLayoutMobile } from './constants';

const initialWordOfTheDay = dirtyWords[Math.floor(Math.random()*dirtyWords.length)].toUpperCase();

function App() {
  const [activeRow, setActiveRow] = useState(1);
  const [guesses, updateGuesses] = useState(initialGuesses);
  const [buttonTheme, updateButtonTheme] = useState(initialButtonTheme);
  const [wordOfTheDay, setWordOfTheDay] = useState(initialWordOfTheDay);
  const previousActiveRow = usePrevious(activeRow);
  const {width: screenWidth, height} = useWindowSize();
  const keyboard = useRef();
  const alert = useAlert();

  console.log(wordOfTheDay);

  const resetGame = useCallback(() => {
    setActiveRow(1);
    updateGuesses(initialGuesses);
    updateButtonTheme(initialButtonTheme);
    setWordOfTheDay(dirtyWords[Math.floor(Math.random()*dirtyWords.length)].toUpperCase());
  }, [setActiveRow, updateGuesses, updateButtonTheme]);

  const updateFromInput = useCallback((input) => {
    updateGuesses(prevGuesses => ({
      ...prevGuesses,
      [activeRow]: input
    }));
  }, [activeRow, updateGuesses]);

  const handleChange = useCallback((input) => {
    updateFromInput(input);
  }, [updateFromInput]);

  const handlePressEnter = useCallback(() => {
    const input = keyboard.current.getInput();
    if (input === wordOfTheDay) {
      alert.show("Good job!!", {
        hasPlayAgain: true,
        onClose: () => { resetGame(); },
        timeout: 0,
      });
      setActiveRow(activeRow+1);
    }
    else if (input.length !== 5) {
      alert.show("Not enough letters");
    }
    else if (input.length === 5 && !wordList.includes(input.toLowerCase())) {
      alert.show("Word not in dictionary");
    }
    else if (activeRow === 6) {
      alert.show("Better luck next time!", {
        hasPlayAgain: true,
        onClose: () => { resetGame(); },
        timeout: 0,
      });
      setActiveRow(activeRow+1);
    }
    else {
      setActiveRow(activeRow+1);
    }
  }, [activeRow, setActiveRow, alert, wordOfTheDay, resetGame]);

  const handleKeyPress = useCallback((button) => {
    if (button === '{enter}') {
      handlePressEnter();
    }
  }, [handlePressEnter]);

  useEffect(() => {
    function handleKeyDown(e) {
      const input = keyboard.current.getInput();
      if (e.keyCode === 13) {
        handlePressEnter();
      }
      if (e.keyCode === 8) {
        const newInput = input.slice(0, -1);
        keyboard.current.setInput(newInput);
        updateFromInput(newInput);
      }
      if (input.length < 5 && e.keyCode >= 65 && e.keyCode <= 90) {
        const newInput = input + String.fromCharCode(e.keyCode);
        keyboard.current.setInput(newInput);
        updateFromInput(newInput);
      }
    }

    document.addEventListener('keydown', handleKeyDown);

    // Don't forget to clean up
    return function cleanup() {
      document.removeEventListener('keydown', handleKeyDown);
    }
  }, [keyboard, updateFromInput, handlePressEnter]);


  useEffect(() => {
    if (activeRow > 1 && activeRow !== previousActiveRow) {
      const input = keyboard.current.getInput();
      const newKeyboardClasses = input.split('').reduce((acc, letter, index) => {
        if (letter === wordOfTheDay[index]) {
          return {...acc, 'correct-position': [...acc['correct-position'], letter]};
        }
        else if (wordOfTheDay.includes(letter)) {
          return {...acc, 'correct-letter': [...acc['correct-letter'], letter]};
        }
        else {
          return {...acc, 'letter-not-in-word': [...acc['letter-not-in-word'], letter]};
        }
      }, {
        'correct-letter': [],
        'correct-position': [],
        'letter-not-in-word': [],
        'hidden-letter': [],
      });
      updateButtonTheme(prevButtonTheme => {
        return prevButtonTheme.map(theme => ({
          class: theme.class,
          buttons: [...theme.buttons.split(' '), ...newKeyboardClasses[theme.class]].join(' '),
        }));
      });
      keyboard.current.clearInput();
    }
  }, [activeRow, previousActiveRow, keyboard, wordOfTheDay, buttonTheme, updateButtonTheme]);

  return (
    <div className="App">
      <h3 className='title'>WORDLE</h3>
      <div className="words-table">
        <Row id="1" word={guesses[1]} isActive={activeRow === 1} isLocked={activeRow > 1 } wordOfTheDay={wordOfTheDay}></Row>
        <Row id="2" word={guesses[2]} isActive={activeRow === 2} isLocked={activeRow > 2 } wordOfTheDay={wordOfTheDay}></Row>
        <Row id="3" word={guesses[3]} isActive={activeRow === 3} isLocked={activeRow > 3 } wordOfTheDay={wordOfTheDay}></Row>
        <Row id="4" word={guesses[4]} isActive={activeRow === 4} isLocked={activeRow > 4 } wordOfTheDay={wordOfTheDay}></Row>
        <Row id="5" word={guesses[5]} isActive={activeRow === 5} isLocked={activeRow > 5 } wordOfTheDay={wordOfTheDay}></Row>
        <Row id="6" word={guesses[6]} isActive={activeRow === 6} isLocked={activeRow > 6 } wordOfTheDay={wordOfTheDay}></Row>
      </div>
    <Keyboard
      keyboardRef={r => (keyboard.current = r)}
      layout={{ default: screenWidth > 500 ? keyboardLayout : keyboardLayoutMobile }}
      display={keyboardDisplay}
      physicalKeyboardHighlight={true}
      maxLength={5}
      onChange={handleChange}
      onKeyPress={handleKeyPress}
      buttonTheme={buttonTheme}
    />
    </div>
  );
}

export default App;
