import React from 'react';
import ReactDOM from 'react-dom';
import { positions, Provider } from "react-alert";
import './index.css';
import App from './App';

const AlertTemplate = ({ style, options = {}, message, close }) => (
  <div className='alert'>
    {message}
    {options.hasPlayAgain && <button className="play-again-button" onClick={close}>Play again?</button>}
  </div>
)

const options = {
  timeout: 1500000000,
  position: positions.TOP_CENTER,
  offset: '30px',
};

ReactDOM.render(
  <React.StrictMode>
    <Provider template={AlertTemplate} {...options}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

