import { useCallback } from 'react';

import './App.css';

function Row(props) {
  const { id, word, isActive, isLocked, wordOfTheDay } = props;

  const generateClassNames = useCallback((letterPosition) => {
    const baseClassName = ['letter'];
    if (isActive) {
      if (letterPosition === 4 && word.length >= 4) {
        baseClassName.push('highlighted');
      }
      else if (word.length === letterPosition) {
        baseClassName.push('highlighted');
      }
    }
    else if (isLocked) {
      if (word[letterPosition] === wordOfTheDay[letterPosition]) {
        baseClassName.push('correct-position');
      }
      else if (wordOfTheDay.includes(word[letterPosition])) {
        baseClassName.push('correct-letter');
      }
      else {
        baseClassName.push('letter-not-in-word');
      }
    }
    return baseClassName.join(' ');
  }, [isLocked, wordOfTheDay, isActive, word]);

  return (
    <div className="Row">
      <div className={generateClassNames(0)}>{word[0] || ''}</div>
      <div className={generateClassNames(1)}>{word[1] || ''}</div>
      <div className={generateClassNames(2)}>{word[2] || ''}</div>
      <div className={generateClassNames(3)}>{word[3] || ''}</div>
      <div className={generateClassNames(4)}>{word[4] || ''}</div>
    </div>
  );
}

export default Row;